import React, { useEffect, useState } from 'react';
import ServicesItem from './ServicesItem';

const Services = () => {
    const [data, setData] = useState([]);
    useEffect(() => {
        fetch('https://genius-car-server-five-ebon.vercel.app/services')
        .then(res => res.json())
        .then(data => setData(data))
    },[])
    return (
        <div className='mb-16'>
            <div className='text-center mb-8'>
                <p className='text-xl text-orange-500 font-bold'>About Us</p>
                <h1 className="text-4xl font-bold ">Our Services Area</h1>
                <p className='max-w-3xl m-auto '>the majority have suffered alteration in some form, by injected humour, or randomised words which don't look even slightly believable. </p>
            </div>
            <div className='grid grid-cols-1 md:grid-cols-2 lg:md:grid-cols-3 gap-5'>
                {
                    data.map(item => <ServicesItem key={item._id} item={item}/>)
                }
            </div>
        </div>
    );
};

export default Services;